<script>
// 日期
const tdate = new Date();
const mdate = {
  time: tdate.getTime(),
  year: tdate.getFullYear(),
  month: tdate.getMonth() + 1,
  date: tdate.getDate(),
  day: tdate.getDay(),
};
const isBirthday = mdate.month == 7 && mdate.date == 28 ? true : false;
// 博客图片地址前缀
const imagePrefix = "https://gallery.panyizhe.com/";
// "https://cheeseburgerim.gitee.io/chai_gallery/";

// 部署域名
const domain = "https://panyizhe.com/";

// 当前域名
const origin = window.location.origin;

// 博客音频地址前缀
const audioPrefix = origin;

// 博客文章地址前缀
const articlePrefix = "https://panyizhe.gitee.io/chai_post/";

// Hexo博客地址前缀
const hexoPrefix = "https://post.panyizhe.com/";

// 博客默认标题
const defaultTitle = "CheeseburgerIM - Tony Pan";

// 情侣空间地址前缀
const belovedPrefix = "https://beloved.panyizhe.com/";

// 修改博客标题
function alterTitle(name) {
  if (name === "Home") {
    document.title = this.defaultTitle;
  } else {
    document.title = name + " | CheeseburgerIM";
  }
}

export default {
  // constant
  mdate,
  isBirthday,
  imagePrefix,
  domain,
  origin,
  audioPrefix,
  articlePrefix,
  hexoPrefix,
  defaultTitle,
  belovedPrefix,
  // function
  alterTitle,
};
</script>

