<template>
  <div
    id="DrawerContent"
    v-loading="timeline.loading"
    element-loading-background="var(--body-color)"
  >
    <div id="Beloved">
      <div id="bg"></div>
      <img id="pyz" src="../assets/beloved/pyz.png" alt="pyz" />
      <div id="heart"></div>
      <img id="zyq" src="../assets/beloved/zyq.png" alt="zyq" />
    </div>
    <Poem :content="content" />
    <div id="timeline">
      <div class="radio">
        排序：
        <el-radio-group v-model="timeline.reverse">
          <el-radio :label="true">倒序</el-radio>
          <el-radio :label="false">正序</el-radio>
        </el-radio-group>
      </div>
      <el-timeline :reverse="timeline.reverse">
        <el-timeline-item
          v-for="(activity, index) in timeline.activities"
          :key="index"
          :icon="activity.icon"
          :type="activity.type"
          :color="activity.color"
          :size="activity.size"
          :timestamp="activity.timestamp"
          placement="top"
        >
          <div v-html="activity.content"></div>
        </el-timeline-item>
      </el-timeline>
    </div>
  </div>
</template>
  
  <script>
import Poem from "./Utils/Poem.vue";
export default {
  name: "DrawerContent",
  computed: {
    content() {
      var d = new Date(this.$store.state.startDate.dateString);
      var day = (this.GLOBAL.mdate.time - d.getTime()) / (24 * 60 * 60 * 1000);
      var time = Math.floor(day);
      return {
        title: "Beloved",
        sentence: `我们已经在一起${time}天了`,
        derive: this.$store.state.startDate.dateString,
        lines: [
          "👻春潮遇三月 荒野遇山雀 我遇见你🐇",
          "📒银河遇初雪 我还没书写 你的消息🖋️",
          "💖光之于海底 火之于夏季 我之于你🐬",
          "☀️你是梦是雨 是我干渴的 白天夜里🌙",
        ],
      };
    },
  },
  data() {
    return {
      timeline: {
        loading: true,
        reverse: false,
        activities: [],
      },
    };
  },
  methods: {},
  components: {
    Poem,
  },
  mounted() {
    var that = this;
    this.$store.state.myAxios.get("Beloved").then((res) => {
      that.timeline.activities = res.data.activities || [];
      setTimeout(() => {
        that.timeline.loading = false;
      }, 700);
    });
  },
};
</script>
  
<style scoped>
#DrawerContent {
  width: 100%;
  max-width: 700px;
  margin: auto;
}

#Beloved {
  position: relative;
  display: flex;
  justify-content: space-evenly;
  margin-top: 27px;
  transition: 0.7s;
}

#Beloved #bg {
  position: absolute;
  background: url(../assets/beloved/avatar_gif.gif);
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 1;
  transition: 0.7s;
  opacity: 0;
}

#Beloved #heart {
  position: absolute;
  background: url(../assets/beloved/heart.gif);
  width: 30%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  z-index: 3;
  opacity: 0.9;
  transition: 0.7s;
}

#Beloved > img {
  width: 270px;
  height: 270px;
  border-radius: 50%;
  transition: 0.7s;
  position: relative;
  z-index: 2;
}

#Beloved #pyz {
  transform: rotateY(0.5turn);
}

#Beloved:hover #bg {
  opacity: 1;
}
#Beloved:hover #heart {
  opacity: 0;
}
#Beloved:hover > img {
  opacity: 0.85;
}
#Beloved:hover #pyz {
  transform: translateX(30%) rotateY(0.5turn);
}
#Beloved:hover #zyq {
  transform: translateX(-30%);
}

#timeline {
  min-height: 300px;
}

#timeline .radio {
  width: 100%;
  height: 35px;
  padding: 10px 31px;
}

#timeline >>> a {
  color: #007aff;
  font-weight: 700;
}

#timeline >>> .el-timeline-item__tail {
  border-color: #007aff !important;
}

#DrawerContent >>> .el-loading-spinner {
  top: 7%;
  margin-top: unset;
}
</style>